.progress {
  display: block;
  color: #585A5F;
  font-family: "Segoe UI", sans-serif; }
  .progress.determinate {
    padding: 24px 60px; }
  .progress > .progress-text {
    display: block;
    padding-top: 8px;
    font-size: 0.75em; }
  .progress.progress-large > .progress-text {
    display: block;
    padding-top: 28px;
    text-align: center;
    font-size: 1.125em; }
  .progress > .progress-bar {
    display: block;
    width: 100%;
    height: 8px;
    background-color: #E6E6E6;
    overflow: hidden; }
    .progress > .progress-bar > span {
      display: block;
      width: 0%;
      /* Default to 0% progress */
      height: 8px;
      background-color: #00B294; }
  .progress.indeterminate {
    border-top: 0.3em solid #E6E6E6;
    border-right: 0.3em solid #E6E6E6;
    border-bottom: 0.3em solid #E6E6E6;
    border-left: 0.3em solid #00B294;
    display: inline-block;
    font-size: inherit;
    position: relative;
    text-indent: -9999em;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation: spinner 1s infinite linear;
    animation: spinner 1s infinite linear;
    overflow: hidden; }
  .progress.indeterminate, .progress.indeterminate:after {
    border-radius: 50%;
    width: 1em;
    height: 1em; }

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

